<template>
  <v-container class="pa-0" style="background-color:#efe9eb ; height:100%">
    <!-- Header -->
    <PDHeader></PDHeader>

    <!-- Main -->
    <v-container class="px-0">
      <!--  -->
      <FullCalendar ref="calendar" class="calendar" :options="calendarOptions">
      </FullCalendar>

      <!--  -->
      <v-dialog v-model="eventDialog">
        <v-card>

          <v-card-title>
            <span></span>
            <v-btn icon @click="eventDialog = false" class="close-btn">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-card-title>

          <v-card-text>
            <span></span>
            <v-select :items="tagList" v-model="selectedTag"></v-select>

          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="eventDialog = false"></v-btn>
            <v-btn color="blue darken-1" @click="saveEvent"></v-btn>
          </v-card-actions>

        </v-card>
      </v-dialog>

      <!--  -->
      <v-dialog v-model="alertDialog">
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text><br>04-26651175</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue" @click="alertDialog = false"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!--  -->
      <v-dialog v-model="deleteDialog">
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text>?</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue" @click="deleteEvent"></v-btn>
            <v-btn color="blue" @click="deleteDialog = false"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>


    </v-container>

    <!-- Footer -->
    <PDFooter></PDFooter>

  </v-container>
</template>

<script setup>
// Components
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue'
import { ref, reactive, onMounted, computed } from 'vue';
import dayjs from 'dayjs'

//
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import timeGridPlugin from '@fullcalendar/timegrid'
import listPlugin from '@fullcalendar/list'
import esLocale from '@fullcalendar/core/locales/zh-tw'
import axios from 'axios';


// control
const eventDialog = ref(false);
const alertDialog = ref(false);
const deleteDialog = ref(false);

// data
const calendarEventList = ref([]);
const tag = ref([]);
const tagList = ref([]);
onMounted(async () => {
  try {
    const eventResponse = await axios.get('/api/Calendar/get_calendar_event', {
      params: {
        PTSEQ: 1,
        premonth: dayjs().subtract(1, 'month').format('YYYY-MM'),
        nextmonth: dayjs().add(1, 'month').format('YYYY-MM')
      }
    });
    const tagResponse = await axios.get('/api/Calendar/get_calendar_tag');
    const eventData = await eventResponse.data.map(item => {
      return {
        title: item.evt,
        start: item.sdte,
        color: item.clr,
        textColor: item.txtclr
      }
    });
    const tagData = await tagResponse.data.map(item => {
      return item.evt;
    });
    calendarEventList.value = eventData;
    tagList.value = tagData;
    tag.value = tagResponse.data;
  } catch (error) {
    console.log(error);
  }
})


//
const calendar = ref(null);
const calendarOptions = reactive({
  plugins: [dayGridPlugin, interactionPlugin, timeGridPlugin, listPlugin],
  initialView: 'dayGridMonth',
  headerToolbar: {
    start: '', // will normally be on the left. if RTL, will be on the right
    center: 'prebtn,title,nextbtn',
    end: '' // will normally be on the right. if RTL, will be on the left
  },
  customButtons: {
    prebtn: {
      icon: 'chevron-left',
      click: async function () {
        const calendarApi = calendar.value.getApi();
        calendarApi.prev();
        getCalendarEvent()
      }
    },
    nextbtn: {
      icon: 'chevron-right',
      click: async function () {
        const calendarApi = calendar.value.getApi();
        calendarApi.next();
        getCalendarEvent();
      }
    }
  },
  contentHeight: 400,
  locale: esLocale,
  editable: true,
  selectable: true,
  weekends: true,
  events: calendarEventList,
  eventClick: function (element) {
    deleteDialog.value = true;
    selectedTag.value = element.event.title;
    evtStart.value = dayjs(element.event._instance.range.start).format("YYYY-MM-DD");
  },
  dateClick: function (info) {
    eventDialog.value = true;
    evtStart.value = info.date;
  },

})


//data
const evtStart = ref(new Date());
const selectedTag = ref();
const evtTitle = ref();
const evtID = computed(() => {
  const eventID = tag.value.find(item => item.evt === selectedTag.value)
  return eventID ? eventID.evtid : ''
});
const randomNumber = () => {
  const min = 1000;
  const max = 9999;
  const DTASEQ = Math.floor(Math.random() * (max - min + 1)) + min;
  return DTASEQ.toString();
}

// 
const saveEvent = async () => {
  evtTitle.value = selectedTag.value;
  // 
  const formattedDate = ref('');
  const formatDate = () => {
    formattedDate.value = dayjs(evtStart.value).format('YYYY-MM-DD');
  }
  formatDate();
  // post data
  const postData = {
    PTSEQ: 1,
    DTASEQ: randomNumber(),
    EVTID: Number(evtID.value),
    SDTE: dayjs(evtStart.value).format('YYYY-MM-DD')
  };
  try {
    const response = await axios.post('/api/Calendar/insert_calendar', postData);
    console.log(response.data);
  } catch (error) {
    console.error(error);
  }
  getCalendarEvent();

  // select box+

  selectedTag.value = '';
  alertDialog.value = true;
  eventDialog.value = false;
}

// 
const deleteEvent = async () => {
  const postData = {//eslint-disable-line
    PTSEQ: 1,
    EVTID: Number(evtID.value),
    SDTE: evtStart.value
  }
  try {
    const response = await axios.post('/api/Calendar/delete_calendar', postData);
    console.log(response.data);
  } catch (error) {
    console.error(error);
  }
  getCalendarEvent();
  selectedTag.value = '';
  deleteDialog.value = false;
}


const getCalendarEvent = async () => {
  const calendarApi = calendar.value.getApi();
  const selectedPrevDate = dayjs(calendarApi.getDate()).subtract(1, 'month').format('YYYY-MM')
  const selectedNextDate = dayjs(calendarApi.getDate()).add(1, 'month').format('YYYY-MM')
  const eventResponse = await axios.get('/api/Calendar/get_calendar_event', {
    params: {
      PTSEQ: 1,
      premonth: selectedPrevDate,
      nextmonth: selectedNextDate
    }
  });
  const eventData = await eventResponse.data.map(item => {
    return {
      title: item.evt,
      start: item.sdte,
      color: item.clr,
      textColor: item.txtclr
    }
  });
  calendarEventList.value = eventData;
}

</script>
 

<style lang="scss" scoped>
.close-btn {
  width: 40px;
  height: 40px;
  box-shadow: none;
  position: absolute;
  top: 0;
  right: 0;
}

.calendar {
  height: 500px;
}

::v-deep .fc-day-sun,
::v-deep .fc-day-sat {
  color: red;
}
</style>


